.profile-card {
  text-align: center;
  max-width: 300px;
  margin: auto;
  padding: 8pt;
  font-family: sans-serif;
  box-shadow: 0 4px 8px #0003;
}

.apicard {
  max-width: 500px;
  margin: 50px auto auto;
  padding: 2em;
  box-shadow: 0 4px 8px #0003;
}

.curlcode {
  background-color: #eee;
  padding: 30px;
  display: block;
}

.user-title {
  color: gray;
  font-size: 18px;
}

.button-profile {
  color: #fff;
  text-align: center;
  cursor: pointer;
  border: none;
  outline: 0;
  width: 100%;
  padding: 8px;
  font-size: 18px;
  display: inline-block;
}

.logout {
  background-color: #000;
}

.social-link {
  color: #000;
  font-size: 22px;
  text-decoration: none;
}

.button-profile:hover, .social-link:hover {
  opacity: .7;
}

.italic {
  font-style: italic;
}

.hidden-first {
  visibility: hidden;
  animation: 5s forwards fadeIn;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    visibility: visible;
    opacity: 1;
  }
}
